.greetingForm {
    background: #ffffff;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    max-width: 400px;
    width: 100%;
}

.greetingForm h1 {
    font-size: 24px;
    color: #333;
    margin-bottom: 16px;
    text-align: center;
}

.inputAndButtonContainer {
    display: flex;
    gap: 10px;
    margin-bottom: 16px;
}

.input {
    flex: 1;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 16px;
    outline: none;
    transition: border-color 0.3s;
}

.input:focus {
    border-color: #007bff;
}

.errorInput {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 16px;
    border-color: #e74c3c;
}

.button {
    height: 36px;
    text-transform: capitalize;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    padding: 10px 16px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.button:focus {
    outline: 2px solid #0056b3;
}

.button:active {
    background-color: #0056b3;
}

.button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
}

.error {
    color: #e74c3c;
    font-size: 14px;
    margin-bottom: 10px;
    text-align: center;
}

.text {
    font-size: 16px;
    color: #555;
    margin-bottom: 8px;
    text-align: center;
}

.greeting {
    font-size: 18px;
    color: #333;
    font-weight: bold;
    text-align: center;
}